"use client";
import Image from "next/image";
import Link from "next/link";
import { useState } from "react";
import { EyeOff } from "lucide-react";
import { ICONS } from "@/ui/icon";

export default function Home() {
  const [showPassword, setShowPassword] = useState(false);

  return (
    <div className="grid grid-rows-[0px_1fr_20px] items-center justify-items-center min-h-screen sm:pt-[64px] pt-[30px] gap-16 font-[family-name:var(--font-geist-sans)]">
      <Image src="/Logo.svg" alt="Dating logo" width={48} height={88} />
      <main className="bg-[#F9F9F9] p-12 row-start-2 items-center max-w-[600px] text-center">
        <div className="mb-10 sm:pt-0 pt-20 flex flex-col gap-[16px]">
          <h3 className="text-[24px] leading-[32px] font-semibold mb-8">
            Registration
          </h3>
          <div className="w-full">
            <input
              placeholder="Full name"
              type="text"
              id="name"
              className="border-1 rounded-[8px] text-[14px] border-[#D9D9D9] focus:outline-none bg-[#fff] px-[12px] py-[9px] h-[40px] w-full"
            />
          </div>
          <div className="">
            <input
              placeholder="Email"
              type="email"
              id="email"
              className="border-1 rounded-[8px] text-[14px] border-[#D9D9D9] focus:outline-none bg-[#fff] px-[12px] py-[9px] h-[40px] w-full"
            />
          </div>
          <div className="">
            <input
              placeholder="Phone"
              type="tel"
              id="phone"
              pattern="[0-9]{10,15}"
              className="border-1 rounded-[8px] text-[14px] border-[#D9D9D9] focus:outline-none bg-[#fff] px-[12px] py-[9px] h-[40px] w-full"
            />
          </div>
          <div className="relative w-full">
            <input
              placeholder="Password"
              type={showPassword ? "text" : "password"}
              id="password"
              className="border-1 rounded-[8px] text-[14px] border-[#D9D9D9] focus:outline-none bg-[#fff] bg-white px-[12px] pr-[40px] py-[9px] h-[40px] w-full"
            />
            <button
              type="button"
              onClick={() => setShowPassword(!showPassword)}
              className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-500 hover:text-black"
              tabIndex={-1}>
              {showPassword ? (
                <ICONS.NOTVis/>
              ) : (
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="18"
                  height="18"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  className="text-[#8C8C8C]"
                  viewBox="0 0 24 24">
                  <path d="M1 12s4-7 11-7 11 7 11 7-4 7-11 7S1 12 1 12z" />
                  <circle cx="12" cy="12" r="3" />
                </svg>
              )}
            </button>
          </div>

          <div className="flex gap-2">
            <input
              id="agree"
              type="checkbox"
              className="w-4 h-4 mt-1 text-blue-600 border-gray-300 rounded focus:outline-none"
            />
            <label htmlFor="agree" className="text-sm text-left">
              By continuing, you agree to our Terms of Use and Privacy Policy
            </label>
          </div>
        </div>
        <div className="w-full">
          <Link
            href=""
            className="bg-[#1677FF] text-[#fff] rounded-[10px] text-base py-[9px] px-[16px] w-full  leading-6 h-10">
            Continue
          </Link>
        </div>
      </main>
    </div>
  );
}
